.button {
  display: inline-block;
  background-color: $purple;
  border-radius: 30px;
  padding: 10px 20px;
  min-width: 100px;
  text-align: center;
  cursor: pointer;
  transition:
    background-color $duration ease-in-out,
    border-color $duration ease-in-out;
  border: 1px solid transparent;
  @include noselect();

  @media (max-width: $md2) {
    padding: 7px 14px;
  }

  &,
  span {
    color: #fff;
  }

  i,
  span,
  path,
  svg {
    transition: all $duration ease-in-out;
  }

  &:hover {
    background-color: darken($purple, 8%);
  }

  &:active {
    background-color: darken($purple, 12%);
    transform: translateY(1px);
  }

  &--circle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    min-width: 32px;
    padding: 0;
    border-width: 2px;

    @media (max-width: $md2) {
      width: 26px;
      height: 26px;
      min-width: 26px;
    }
  }

  &--black {
    background-color: $black;

    &:hover,
    &:active {
      background-color: darken($color: $black, $amount: 10);
    }
  }

  &--outline {
    background-color: #fff;
    border-color: $purple;

    transition: 0.25s ease-in-out all;

    &,
    span {
      color: $purple;
    }

    svg {
      path {
        fill: $purple;
      }
    }

    &:hover {
      background-color: $purple;

      &,
      span {
        color: #fff;
      }

      svg {
        path {
          fill: #fff;
        }
      }
    }

    &:active {
      background-color: darken($purple, 8%);
    }
  }

  &__delimiter {
    width: 1px;
    height: 25px;
    background-color: rgba(255, 255, 255, 0.25);
    margin-left: 14px;
    margin-right: 14px;
  }

  &--add {
    svg {
      margin-right: 2px;
    }

    span {
      font-weight: 600;
      font-size: 16px;

      @media (max-width: $md2) {
        font-size: 14px;
      }
    }

    &:hover {
      i {
        background-color: #fff;
        color: $purple;
      }
    }

    i {
      display: inline-block;
      border-radius: 30px;
      background-color: $purple;
      color: #fff;
      font-weight: 600;
      width: 22px;
      height: 22px;
      font-style: normal;
      font-size: 13px;
      line-height: 22px;
      position: relative;
      top: -1px;
      left: 3px;

      @media (max-width: $md2) {
        font-size: 11px;
        width: 20px;
        height: 20px;
      }
    }
  }

  &--cart {
    display: flex;
    align-items: center;
    line-height: 23px;
    padding: 12px 25px;

    @media (max-width: $md2) {
      padding: 6px 12px;
      border-radius: 20px;
      min-width: auto;
      width: 100%;
    }

    svg {
      margin-right: 8px;
      margin-bottom: 1px;
    }

    span {
      font-weight: 600;
      font-size: 16px;

      @media (max-width: $md2) {
        font-size: 14px;
        flex: 1 0 auto;
      }
    }
  }
  &--card {
    display: flex;
    align-items: center;
    gap: 5px;

    border: none;
    background-color: #eee;

    transition: 0.25s ease-in-out all;

    @media (any-hover: hover) {
      &:hover {
        background-color: $purple;
      }
    }
  }
}
